import React, { useEffect } from 'react';
import { connect } from 'react-redux';

// Assets
import '../assets/styles/views/Scopes.scss';

// Layout
import AppLayout from '../layouts/AppLayout';

// Components
import Gentable from '../components/GenTable';
//import Button from '../components/Button';

// Actions
import { getAllScopes } from '../actions/scopesActions';

const Scopes = ({ getAllScopes, scopes }) => {

  useEffect(() => {
    getAllScopes();
  }, []);

  return (
    <AppLayout>
      <div className='view__wrapper'>
        <Gentable
          title='Scopes Activos'
          headers={['Id', 'Nombre', 'Tipo', 'Resoruce', 'Fecha de Creación']}
          actions={[
            { name: 'Crear Scope', icon: 'AiOutlinePlus', route: '/app/management/roles/create' },
          ]}
          data={scopes.list}
        />
      </div>
    </AppLayout>
  );
};

const mapStateToProps = (state) => ({
  scopes: state.scopes,
});

const mapDispatchToProps = {
  getAllScopes,
};

export default connect(mapStateToProps, mapDispatchToProps)(Scopes);
