import axios from 'axios';
import moment from 'moment';
import { API_URL } from '../utils/config';

// Types
import { SCOPE_GET_ALL_REQUEST } from '../types/index';

export const getAllScopesRequest = (list) => {
  return {
    type: SCOPE_GET_ALL_REQUEST,
    list,
  };
};

export const getAllScopes = () => {
  return (dispatch) => {
    const config = {
      url: `${API_URL}/api/management/scopes`,
      method: 'get',
    };

    axios(config)
      .then(({ data }) => {

        const cleanData = data.data.map((scope) => {
          console.log('action');
          // eslint-disable-next-line no-param-reassign
          scope.createdAt = moment(scope.createdAt).format('DD-MM-YY hh:mm A');
          return scope;
        });

        dispatch(getAllScopesRequest(cleanData));
      })
      .catch((err) => {
        console.log(err);
      });
  };
};
